<template>
    <div class="container" style="margin-top: 50px; padding-top: 50px;">
        <div class="row">
            <div class="col align-self-start"></div>
            <div class="col align-self-center" style="width: 80%; height: 80vh; display: flex; flex-flow: column; justify-content: center;">
                <form>
                    <div class="alert alert-warning" role="alert" v-if="errorMsg !== ''">
                        {{ errorMsg }}
                    </div>
                    <div class="form-group">
                        <label for="email">Email address</label>
                        <input type="text" class="form-control" id="email" placeholder="Enter email" v-model="email">
                        <small id="emailHelp" class="form-text text-muted"></small>
                    </div>
                    <div class="form-group">
                        <label for="username">Username</label>
                        <input type="text" class="form-control" id="username" placeholder="Enter Username" v-model="username">
                    </div>
                    <div class="form-group">
                        <label for="name">Name</label>
                        <input type="text" class="form-control" id="name" placeholder="Enter Name" v-model="name">
                    </div>
                    <div class="form-group">
                        <label for="hp">Handphone</label>
                        <input type="text" class="form-control" id="hp" placeholder="Enter Handphone" v-model="handphone">
                    </div>
                    <div class="form-group">
                        <label for="address">Address</label>
                        <textarea id="address" name="address" class="form-control" v-model="address"></textarea>
                    </div>
                    <div class="form-group">
                        <label for="gender">Gender</label>
                        <select id="gender" class="form-control" v-model="gender">
                            <option v-for="option in genderOptions" v-bind:value="option.value" v-bind:key="option.value">
                                {{ option.text }}
                            </option>
                        </select>
                    </div>
                    <div class="form-group">
                        <label for="password">Password</label>
                        <div class="input-group">
                            <input :type="passwordFieldType" class="form-control" placeholder="Input Password" id="password" v-model="password">
                            <div class="input-group-prepend" @click="switchVisibility">
                                <button class="input-group-text" id="btnGroupAddon" type="button">Show</button>
                            </div>
                        </div>
                    </div>
                    <div style="display: flex; flex-flow: row; justify-content: space-around">
                        <button type="button" class="btn btn-primary" @click="submit">Submit</button>
                        <router-link to="/" class="btn btn-secondary">Back</router-link>
                    </div>
                </form>
            </div>
            <div class="col align-self-end"></div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios'

export default {
  name: 'Signup',
  data () {
    return {
      password: '',
      passwordFieldType: 'password',
      username: '',
      name: '',
      email: '',
      handphone: '',
      address: '',
      gender: 'm',
      genderOptions: [
        {text: 'Male', value: 'm'},
        {text: 'Female', value: 'f'}
      ],
      errorMsg: ''
    }
  },
  methods: {
    switchVisibility () {
      this.passwordFieldType = this.passwordFieldType === 'password' ? 'text' : 'password'
    },
    submit () {
      axios.post('http://localhost:8080/auth/signup', {
          username: this.username,
          name: this.name,
          email: this.email,
          handphone: this.handphone,
          address: this.address,
          gender: this.gender,
          password: this.password
      }).then((response) => {
        console.log(response)
        this.$router.push('/')
      }).catch((error) => {
        this.errorMsg = error.response.data.msg
      })
    }
  }
}
</script>
